// font
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;900&display=swap');
@font-face {
    font-family: 'AVERTA';
    src: url('../../../resources/img/Events/Marketing/AVERTA-REGULAR.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'AVERTA';
    src: url('../../../resources/img/Events/Marketing/AVERTA-SEMIBOLD.woff') format('woff');
    font-weight: 600;
    font-style: normal;
}
@font-face {
    font-family: 'AVERTA';
    src: url('../../../resources/img/Events/Marketing/AVERTA-BOLD.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}
@font-face {
    font-family: 'AVERTA';
    src: url('../../../resources/img/Events/Marketing/AVERTA-BLACK.woff') format('woff');
    font-weight: 900;
