.notoBold{font-weight: 900;}
.averta{font-family: 'AVERTA';}

//variable
$scroll-page-size: 44;
$transition-base-color: #1D1D1B;
$white-like-background: #eaeaea;
//$section07-transition-width: 9.8958vw;
$section07-transition-width: 32.533vw;
$section09-transition-width: 9.6vw;

//common
body{margin: 0;}
.wrap{margin: 0; padding: 0; font-family: 'Noto Sans KR', sans-serif; line-height: 1.2;}
.wrap{width: 100%; height: 100%;}
.content-body {
	height: calc(100vh * calc(#{$scroll-page-size} + 1));
}
.content-body__container {
	position: sticky;
	height: 100vh;
	top: 0;
	left: 0;
	overflow: hidden;
}
.animation-container {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	overflow: hidden;
}
.sec{height: 100vh; position:relative; overflow: hidden;}
.rel{position: relative;}
.ab{position: absolute;}
.marginDel{margin: 0;}
.white{color: #ffffff;}
.black{color: #000000;}
.dark-gray{background-color: $transition-base-color}
.z-index-10 {z-index: 10;}
.no-display { display: none !important; }
.dot {
	width: 2.667vw;
	height: 2.667vw;
	border-radius: 2.667vw;
	-webkit-border-radius: 2.667vw;
	-moz-border-radius: 2.667vw;
	-ms-border-radius: 2.667vw;
	-o-border-radius: 2.667vw;
	margin-left: 1.5vw;
}
.dotColor01{background-color: #ead7c7;}
.dotColor02, .white-like-background {background-color: $white-like-background;}
.dotColor03{background-color: #282828;}

// sections
.section-default {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
}
.section07-rotation {
	position: absolute;
	top: -25vh;
	left: 0;
	width: 100%;
	height: 150vh;
	transform-origin: 50% 73vh;
}
.section11-rotation {
	position: absolute;
	top: -25vh;
	left: 0;
	width: 100%;
	height: 150vh;
	//transform-origin: 50% calc(93vw + 25vh);
	transform-origin: 50% calc(10vw + 55vh);
}

$sec01-background-color: #191919;
.sec01 {
	z-index: 100;
	background-color: $sec01-background-color;
	.sec01__btn {
		cursor: pointer;
		background-color: transparent;
		padding: 0;
		border: 0;
		width: 58.889vw;
		height: 58.889vw;
		left: 20.5vw;
		top: 35vh;
		z-index: 10;
		backface-visibility: hidden;
		transform:rotateY(0deg);
		-webkit-transform:rotateY(0deg);
		-moz-transform:rotateY(0deg);
		-ms-transform:rotateY(0deg);
		-o-transform:rotateY(0deg);
		.sec01__btn__img{
			width: 100%;
			left: 0;
			top: 0;
			z-index: 1;
		}
		.sec01__btn__light {
			transition: 1s;
			transform: rotateY(0deg);
			-webkit-transform: rotateY(0deg);
			-moz-transform: rotateY(0deg);
			-ms-transform: rotateY(0deg);
			-o-transform: rotateY(0deg);
			.sec01__btn__light__light00{
				left: -20.7vw;
				top: -72vw;
				width: 100vw;
				opacity: 0.4;
			}
			.sec01__btn__light__light00--off {
				animation-name: sec01__light--off, sec01--click, sec01--rotate-backward;
				animation-delay: 1.25s, 0s, 1.25s;
				animation-duration: 0.25s, 0.5s, 1s;
				animation-timing-function: ease;
				animation-direction: alternate;
				animation-fill-mode: forwards;
			}
			.sec01__btn__light__light01{
				left: 59.4vw;
				top: -29.4vw;
				width: 20.533vw;
				height: 59.2vw;
				opacity: 0;
			}
			.sec01__btn__light__light01--on{
				animation-name: sec01__btn__light__light01, sec01--opacity-on, sec01__light--off;
				animation-delay: 0.5s, 0.5s, 1.25s;
				animation-duration: 0.2s, 0.2s, 0.1s;
				animation-timing-function: ease;
				animation-direction: alternate;
				animation-fill-mode: forwards;
			}
			.sec01__btn__light__light02{
				left: -20vw;
				top: 29.8vw;
				width: 100vw;
				opacity: 0;
			}
			.sec01__btn__light__light02--on{
				animation-name: sec01__btn__light__light02, sec01--opacity-on, sec01__light--off;
				animation-delay: 0.65s, 0.65s, 1.25s;
				animation-duration: 0.2s, 0.2s, 0.1s;
				animation-timing-function: ease;
				animation-direction: alternate;
				animation-fill-mode: forwards;
			}
			.sec01__btn__light__light03{
				left: -20vw;
				top: -29.4vw;
				width: 20.533vw;
				height: 59.2vw;
				opacity: 0;
			}
			.sec01__btn__light__light03--on{
				animation-name: sec01__btn__light__light03, sec01--opacity-on, sec01__light--off;
				animation-delay: 0.85s, 0.85s, 1.25s;
				animation-duration: 0.2s, 0.2s, 0.1s;
				animation-timing-function: ease;
				animation-direction: alternate;
				animation-fill-mode: forwards;
			}
			.sec01__btn__light__light04{
				left: -20vw;
				top: -71.8vw;
				width: 100vw;
				opacity: 0;
			}
			.sec01__btn__light__light04--on{
				animation-name: sec01__btn__light__light04, sec01--opacity-on, sec01__light--off;
				animation-delay: 1.05s, 1.05s, 1.25s;
				animation-duration: 0.2s, 0.2s, 0.1s;
				animation-timing-function: ease;
				animation-direction: alternate;
				animation-fill-mode: forwards;
			}
		}
	}
	.sec01__click01{
		animation-name: sec01--click, sec01--rotate-backward;
		animation-delay: 0s, 1.25s;
		animation-duration: 0.5s, 1s;
		animation-timing-function: ease;
		animation-direction: alternate;
		animation-fill-mode: none, forwards;
	}
	.sec01__arrowFont {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		//bottom: 4.5vw;
		top: calc(35vh + 58.889vw + 3vh);
		left: 46vw;
		font-weight: 900;
		.sec01__arrowFont__arrow {
			width: 1.5vw;
		}
		.sec01__arrowFont__font {
			font-size: 2vw;
			margin: 0.833vw 0 0;
		}
	}
	.sec01__font {
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		width: 58.889vw;
		height: 58.889vw;
		left: 20.5vw;
		top: 35vh;
		backface-visibility: hidden;
		background-color: #ff6900;
		color: #fff;
		font-size: 8.067vw;
		transform: rotateY(-180deg);
		-webkit-transform: rotateY(-180deg);
		-moz-transform: rotateY(-180deg);
		-ms-transform: rotateY(-180deg);
		-o-transform: rotateY(-180deg);
	}
	.sec01__click03 {
		animation-name: sec01--rotate-forward, sec01__font;
		animation-delay: 1.25s, 2.25s;
		animation-duration: 1s, 0.6s;
		animation-timing-function: ease;
		animation-direction: alternate;
		animation-fill-mode: forwards;
	}
	@keyframes sec01--click {
		0%   {transform: scale(1);}
		50%  {transform: scale(0.9);}
		100% {transform: scale(1);}
	}
	@keyframes sec01--rotate-backward {
		0%   {transform: rotateY(0deg);}
		100% {transform: rotateY(180deg);}
	}
	@keyframes sec01--rotate-forward {
		0%   {transform: rotateY(-180deg);}
		100% {transform: rotateY(0deg);}
	}
	@keyframes sec01__light--off {
		0%   {opacity: 0.4;}
		100% {opacity: 0;}
	}
	@keyframes sec01--opacity-on {
		0%   {opacity: 0;}
		100% {opacity: 1;}
	}
	@keyframes sec01__btn__light__light01 {
		0%   {height: 0vw;}
		100% {height: 59.2vw;}
	}
	@keyframes sec01__btn__light__light02 {
		0%   {left: -26vw;}
		100% {left: -20vw;}
	}
	@keyframes sec01__btn__light__light03 {
		0%   {top: -25.4vw;}
		100% {top: -29.4vw;}
	}
	@keyframes sec01__btn__light__light04 {
		0%   {left: -22vw;}
		100% {left: -20vw;}
	}
	@keyframes sec01__font {
		0%   {font-size: 8.067vw; left: 20.5vw; top: 35vh; width: 58.889vw; height: 58.889vw; }
		100% {font-size: 13.067vw; left: 0; top: 0; width: 100vw; height: 100vh; line-height: 100vh; cursor: unset;}
	}
	}
	.sec01__left-box {
	z-index: 100;
	visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
	width: 21vw;
	height: 100vh;
	background-color: $sec01-background-color;
	}
	.sec01__left-box--move {
	animation-name: sec01__left-box;
	animation-delay: 2.25s;
	animation-duration: 1s;
	animation-timing-function: ease;
	animation-direction: alternate;
	animation-fill-mode: forwards;
	}
	.sec01__right-box {
	z-index: 100;
	visibility: hidden;
	position: absolute;
	top: 0;
	right: 0;
	width: 21vw;
	height: 100vh;
	background-color: $sec01-background-color;
	}
	.sec01__right-box--move {
	animation-name: sec01__right-box;
	animation-delay: 2.25s;
	animation-duration: 1s;
	animation-timing-function: ease;
	animation-direction: alternate;
	animation-fill-mode: forwards;
	}
	.sec01__top-box {
	z-index: 100;
	visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	//height: 21.7vh;
	height: 35vh;
	background-color: $sec01-background-color;
	}
	.sec01__top-box--move {
	animation-name: sec01__top-box;
	animation-delay: 2.25s;
	animation-duration: 1s;
	animation-timing-function: ease;
	animation-direction: alternate;
	animation-fill-mode: forwards;
	}
	.sec01__bottom-box {
	z-index: 100;
	visibility: hidden;
	position: absolute;
	top: calc(35vh + 58.889vw);
	left: 0;
	width: 100%;
	//height: 42.5vh;
	//height: 27.3735vw;
	height: calc(100vh - 35vh - 58.889vw);
	background-color: $sec01-background-color;
	}
	.sec01__bottom-box--move {
	animation-name: sec01__bottom-box;
	animation-delay: 2.25s;
	animation-duration: 1s;
	animation-timing-function: ease;
	animation-direction: alternate;
	animation-fill-mode: forwards;
	}
	
	@keyframes sec01__left-box {
	0%   { left: 0; visibility: visible }
	100% { left: -21vw; visibility: visible }
	}
	@keyframes sec01__right-box {
	0%   { right: 0; visibility: visible }
	100% { right: -21vw; visibility: visible }
	}
	@keyframes sec01__top-box {
	0%   { top: 0; visibility: visible }
	100% { top: -35vh; visibility: visible }
	}
	@keyframes sec01__bottom-box {
	0%   { top: calc(35vh + 58.889vw); visibility: visible }
	100% { top: 100vh; visibility: visible }
	}
	.sec01--off {
	animation-name: sec01--off;
	animation-delay: 2.25s;
	animation-duration: 0.6s;
	animation-timing-function: ease;
	animation-direction: alternate;
	animation-fill-mode: forwards;
	}
	@keyframes sec01--off {
	0%   { z-index: 100; background-color: $sec01-background-color; visibility: visible }
	100% { z-index: 4; background-color: transparent; visibility: hidden }
	}

.sec02 {
