	.sec02__circle01{
		left: -21vw;
		bottom: 7vw;
		height: 75vw;
		width: 75vw;
		border-radius: 75vw;
		border: 0.1vw solid rgba(255, 255, 255, 0.2);
		z-index: 2;
		transform-origin: 50% 50%;
