		animation: sec02__circle01 3s linear infinite;
		-webkit-animation: sec02__circle01 3s linear infinite;
		-webkit-border-radius: 75vw;
		-moz-border-radius: 75vw;
		-ms-border-radius: 75vw;
		-o-border-radius: 75vw;
		-webkit-transform: rotate(-106deg);
		-moz-transform: rotate(-106deg);
		-ms-transform: rotate(-106deg);
		-o-transform: rotate(-106deg);
}
	.sec02__circle01:after{
		content: "";
