		height: 38vw;
		width: 38vw;
		border-radius: 38vw;
		border: 0.1vw solid rgba(255, 255, 255, 0.2);
		z-index: 2;
		transform-origin: 50% 50%;
		transform: rotate(-60deg);
		animation: sec02__circle02 2.8s linear infinite;
		-webkit-animation: sec02__circle02 2.8s linear infinite;
		-webkit-border-radius: 38vw;
		-moz-border-radius: 38vw;
		-ms-border-radius: 38vw;
		-o-border-radius: 38vw;
}
	.sec02__circle02:after{
		content: "";
		position: absolute;
		top: 20vw;
		right: 4vw;
		width: 3.24vw;
		height: 3.24vw;
		border-radius: 3.24vw;
		background-color: rgba(255,255,255,0.2);
		-webkit-border-radius: 3.24vw;
		-moz-border-radius: 3.24vw;
		-ms-border-radius: 3.24vw;
		-o-border-radius: 3.24vw;
}
	@keyframes sec02__circle01{
		0%{ transform:rotate(-106deg); -webkit-transform:rotate(-106deg); -moz-transform:rotate(-106deg); -ms-transform:rotate(-106deg); -o-transform:rotate(-106deg); }
		10%{ transform:rotate(-10deg); -webkit-transform:rotate(-10deg); -moz-transform:rotate(-10deg); -ms-transform:rotate(-10deg); -o-transform:rotate(-10deg); }
		50%{ transform:rotate(0deg); -webkit-transform:rotate(0deg); -moz-transform:rotate(0deg); -ms-transform:rotate(0deg); -o-transform:rotate(0deg); }
		70%{ transform:rotate(230deg); -webkit-transform:rotate(230deg); -moz-transform:rotate(230deg); -ms-transform:rotate(230deg); -o-transform:rotate(230deg); }
		75%{ transform:rotate(240deg); -webkit-transform:rotate(240deg); -moz-transform:rotate(240deg); -ms-transform:rotate(240deg); -o-transform:rotate(240deg); }
		100%{ transform:rotate(250deg); -webkit-transform:rotate(250deg); -moz-transform:rotate(250deg); -ms-transform:rotate(250deg); -o-transform:rotate(250deg); }
	}
	@keyframes sec02__circle02{
