		20%{ transform:rotate(80deg); -webkit-transform:rotate(80deg); -moz-transform:rotate(80deg); -ms-transform:rotate(80deg); -o-transform:rotate(80deg); }
		55%{ transform:rotate(90deg); -webkit-transform:rotate(90deg); -moz-transform:rotate(90deg); -ms-transform:rotate(90deg); -o-transform:rotate(90deg); }
		80%{ transform:rotate(280deg); -webkit-transform:rotate(280deg); -moz-transform:rotate(280deg); -ms-transform:rotate(280deg); -o-transform:rotate(280deg); }
		100%{ transform:rotate(290deg); -webkit-transform:rotate(290deg); -moz-transform:rotate(290deg); -ms-transform:rotate(290deg); -o-transform:rotate(290deg); }
	}
